import { metas } from "./meta2";

export function getSectionOverrides(langKey: string, page: string, section: string) {
  const fullPath = `${langKey}.pages.${page}.section.${section}`;
  const meta = metas?.[fullPath];
  const map = meta?.["custom-editors"];
  if (!map) return [];

  return Object.entries(map).map(([editorKey, subpaths]) => ({
    editorKey,
    sectionPath: fullPath,
    subpaths: subpaths as string[],
  }));
}

export function getCustomEditorOverride(fullPath: string) {
  const parts = fullPath.split(".");

  // Walk upward to find a containerPath that exists in metas
  for (let i = parts.length; i >= 1; i--) {
    const containerPath = parts.slice(0, i).join(".");
    const meta = metas?.[containerPath];
    const map = meta?.["custom-editors"];
    if (!map) continue;

    // IMPORTANT:
    // fieldKey must be the FIRST key after the containerPath,
    // NOT deeper like ".0" or ".text"
    const fieldKey = parts[i];
    if (!fieldKey) return null;

    for (const [editorKey, keys] of Object.entries(map)) {
      if ((keys as string[]).includes(fieldKey)) {
        return { editorKey, containerPath, fieldKey };
      }
    }
  }

  return null;
}
