import React, { useState } from "react";
import ProtectedRoute from "../../componets/shared/ProtectedRoute/ProtectedRoute";
import MiniStudio from "../../componets/cms/MiniStudio";
import RoleRoutes from "../../componets/shared/ProtectedRoute/RoleRoutes";
import MiniStudioShell from "../../componets/cms/MiniStudioShell";
import PageEditor from "../../componets/cms/PageEditor";
import { metadata } from "../../content/metadata";
import { contentDynamic as content } from "../../content/content";
import { getQstyle } from "../../componets/cms/mini-studio/qstyle";

export default function Editor() {

  const [editor, setEditor] = useState<any>({ metadata, content })
  return <RoleRoutes role="admin">
    <MiniStudioShell>

      <PageEditor
        data={editor}
        onMetadataSave={(nextMetadata) => {
          setEditor((prev: any) => ({
            ...prev,
            metadata: nextMetadata,
          }));
        }}
      />
    </MiniStudioShell>
  </RoleRoutes>;
}
