

import { getContent } from "./content";

export function getPrev(path: string, lang: "en" | "vn" = "vn") {
  const root = getContent(lang);

  return path
    .split(".")
    .reduce((acc: any, key) => acc?.[key], root) ?? {};
}

export function withPrev<T extends object>(
  path: string,
  override: Partial<T>,
  lang: "en" | "vn" = "vn"
): T {
  const base = getPrev(path, lang);
  return {
    ...base,
    ...override,
  } as T;
}

type Patch = any;

export function applyPatch(base: any, patch: Patch): any {
  // Replace entirely
  if (patch && typeof patch === "object" && "@" in patch) {
    return patch["@"] ;
  }

  // Primitive  replace
  if (
    patch === null ||
    typeof patch !== "object" ||
    Array.isArray(patch) === false && typeof base !== "object"
  ) {
    return patch;
  }

  // Array handling
  if (Array.isArray(base)) {
    const result = [...base];

    for (const [key, value] of Object.entries(patch)) {
      const index = Number(key);
      if (!Number.isNaN(index)) {
        result[index] = applyPatch(base[index], value);
      }
    }

    return result;
  }

  // Object handling
  const result: any = { ...base };

  for (const [key, value] of Object.entries(patch)) {
    result[key] = applyPatch(base[key], value);
  }

  return result;
}

export function withPrevPatch<T>(
  path: string,
  patch: Patch,
  lang: "en" | "vn" = "vn"
): T {
  const base = getPrev(path, lang);
  return applyPatch(base, patch);
}

