import React from "react";
import { TextInput, Stack, Button, Text, Modal, Group, Progress, Notification } from "@mantine/core";

function isTikTok(url?: string) {
  return !!url && typeof url === "string" && url.includes("tiktok.com");
}

function getYouTubeEmbed(url?: string) {
  if (!url || typeof url !== "string") return "";
  if (url.includes("youtube.com/embed")) return url;
  const watchMatch = url.match(/v=([^&]+)/);
  if (watchMatch) return `https://www.youtube.com/embed/${watchMatch[1]}`;
  const shortMatch = url.match(/youtu\.be\/([^?]+)/);
  if (shortMatch) return `https://www.youtube.com/embed/${shortMatch[1]}`;
  return url;
}

export function VideoEditor({ label, videoUrl, locked, onChange }: any) {
  const [modalOpen, setModalOpen] = React.useState(false);
  const [tempUrl, setTempUrl] = React.useState<string>("");
  const [saving, setSaving] = React.useState(false);
  const [showSuccess, setShowSuccess] = React.useState(false);

  const currentUrl = videoUrl || "";

  const handleSave = () => {
    setSaving(true);
    setTimeout(() => {
      onChange?.(tempUrl);
      setSaving(false);
      setShowSuccess(true);
      setTimeout(() => {
        setShowSuccess(false);
        setModalOpen(false);
        setTempUrl("");
      }, 1500);
    }, 800);
  };

  const handleCancel = () => {
    setModalOpen(false);
    setTempUrl("");
  };

  const handleEditClick = () => {
    setTempUrl(currentUrl);
    setModalOpen(true);
  };

  const renderPreview = (url?: string) => {
    if (!url || typeof url !== "string") return <Text color="dimmed" size="sm">No video</Text>;

    if (isTikTok(url)) {
      return (
        <div style={{ 
          border: "1px solid #ced4da", 
          borderRadius: 8, 
          padding: 16,
          textAlign: "center",
          minHeight: 200,
          display: "flex",
          alignItems: "center",
          justifyContent: "center"
        }}>
          <div>
            <Text size="sm" mb="xs">TikTok Video</Text>
            <Text size="xs" color="dimmed">{url}</Text>
          </div>
        </div>
      );
    }

    const embedUrl = getYouTubeEmbed(url);
    return (
      <div style={{ 
        position: "relative", 
        paddingBottom: "56.25%", 
        height: 0,
        borderRadius: 8,
        overflow: "hidden"
      }}>
        <iframe
          src={embedUrl}
          title="Video Preview"
          frameBorder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
          style={{
            position: "absolute",
            top: 0,
            left: 0,
            width: "100%",
            height: "100%"
          }}
        />
      </div>
    );
  };

  return (
    <>
      <Stack spacing="xs">
        {renderPreview(currentUrl)}
        
        <TextInput
          label="Video URL"
          value={currentUrl}
          readOnly
          size="xs"
        />

        <Button
          onClick={handleEditClick}
          disabled={!!locked}
          variant="filled"
        >
          Edit Video
        </Button>
      </Stack>

      <Modal
        opened={modalOpen}
        onClose={handleCancel}
        title="Edit Video"
        size="lg"
        centered
      >
        <Stack spacing="md">
          <TextInput
            label="Video URL"
            placeholder="https://www.youtube.com/watch?v=... or https://www.tiktok.com/..."
            value={tempUrl}
            onChange={(e) => setTempUrl(e.currentTarget.value)}
            description="Supports YouTube and TikTok URLs"
          />

          {tempUrl && (
            <>
              <Text size="sm" weight={500}>Live Preview:</Text>
              {renderPreview(tempUrl)}
            </>
          )}

          {saving && <Progress value={100} animated />}

          {showSuccess && (
            <Notification color="green" onClose={() => setShowSuccess(false)} withCloseButton={false}>
              Video saved successfully
            </Notification>
          )}

          <Group justify="flex-end" mt="md">
            <Button variant="subtle" onClick={handleCancel} disabled={saving}>
              Cancel
            </Button>
            <Button
              onClick={handleSave}
              disabled={!tempUrl || saving}
              loading={saving}
            >
              Save
            </Button>
          </Group>
        </Stack>
      </Modal>
    </>
  );
}
