import { TextInput } from "@mantine/core";
import React from "react";

export function LinkEditor({
  label,
  defaultValue,
  locked,
  onChange,
}: any) {
  return (
    <TextInput
      label={label}
      defaultValue={defaultValue}
      disabled={locked}
      placeholder="https://example.com"
      onChange={(e) => onChange?.(e.currentTarget.value)}
    />
  );
}
