import React from "react";
import {
  TextInput,
  Image,
  Stack,
  Button,
  Text,
  Modal,
  Group,
  SegmentedControl,
  Progress,
  Notification,
} from "@mantine/core";

function buildWebpUrlFromUploadPath(REPO_PAGES_BASE: string, uploadPath: string) {
  // uploadPath = "originals/1768592255761-cuisine2.png"
  const filename = uploadPath.split("/").pop() || "";
  const dotIndex = filename.lastIndexOf(".");
  const baseName = dotIndex >= 0 ? filename.slice(0, dotIndex) : filename;

  // GitHub Actions outputs: images/<same-base>.webp
  return `${REPO_PAGES_BASE}/images/${baseName}.webp?v=${Date.now()}`;
}

async function checkUrlExists(url: string) {
  try {
    const res = await fetch(url, { method: "HEAD" });
    return res.ok;
  } catch {
    return false;
  }
}

async function waitForWebp(
  url: string,
  writeLog: (msg: string) => void,
  maxAttempts = 30,
  delayMs = 2000
) {
  writeLog(" Waiting for WEBP output...");

  for (let attempt = 1; attempt <= maxAttempts; attempt++) {
    const ok = await checkUrlExists(url);
    if (ok) {
      writeLog(` WEBP ready! (${attempt}/${maxAttempts})`);
      return true;
    }

    writeLog(` Not ready yet... (${attempt}/${maxAttempts})`);
    await new Promise((r) => setTimeout(r, delayMs));
  }

  writeLog(" WEBP not found after waiting. Action may still be running.");
  return false;
}

export function ImageEditor({
  label,
  src,
  alt,
  locked,
  onSrcChange,
  onAltChange,
}: any) {
  // =========================
  // CONFIG
  // =========================
  const endpoint =
    "https://curly-pond-edc4.snowy-frost-b196.workers.dev/api/upload-image";

  const REPO_PAGES_BASE = "https://mtcmarketing.github.io/images--tinh-hoa-viet";

  // =========================
  // STATE
  // =========================
  const [modalOpen, setModalOpen] = React.useState(false);
  const [mode, setMode] = React.useState<"upload" | "url">("upload");

  const [tempSrc, setTempSrc] = React.useState<string>("");
  const [tempAlt, setTempAlt] = React.useState<string>("");

  const [uploading, setUploading] = React.useState(false);
  const [uploadComplete, setUploadComplete] = React.useState(false);

  const [saving, setSaving] = React.useState(false);
  const [showSuccess, setShowSuccess] = React.useState(false);

  const [dragOver, setDragOver] = React.useState(false);
  const inputRef = React.useRef<HTMLInputElement | null>(null);

  // console + results
  const [log, setLog] = React.useState<string>("");
  const [result, setResult] = React.useState<any>(null);
  const [webpUrl, setWebpUrl] = React.useState<string | null>(null);
  const [webpReady, setWebpReady] = React.useState(false);

  //  local display state so UI updates immediately
  const [displaySrc, setDisplaySrc] = React.useState<string>(src || "");
  const [displayAlt, setDisplayAlt] = React.useState<string>(alt || "");

  const writeLog = (msg: string) => {
    setLog((prev) => (prev ? prev + "\n" + msg : msg));
  };

  // keep display synced when parent updates
  React.useEffect(() => {
    setDisplaySrc(src || "");
  }, [src]);

  React.useEffect(() => {
    setDisplayAlt(alt || "");
  }, [alt]);

  // When modal opens, preload existing src/alt into temp state
  React.useEffect(() => {
    if (!modalOpen) return;

    setTempSrc(displaySrc || "");
    setTempAlt(displayAlt || "");
    setUploadComplete(!!displaySrc);

    setUploading(false);
    setSaving(false);
    setShowSuccess(false);

    setLog("");
    setResult(null);
    setWebpUrl(null);
    setWebpReady(false);
  }, [modalOpen]);

  // =========================
  // UPLOAD TO WORKER
  // =========================
  const uploadToWorker = async (file: File) => {
    setUploading(true);
    setResult(null);
    setWebpUrl(null);
    setWebpReady(false);

    try {
      writeLog(" Uploading to Worker...");

      const form = new FormData();
      form.append("file", file);

      const res = await fetch(endpoint, {
        method: "POST",
        body: form,
      });

      const text = await res.text();

      let json: any = null;
      try {
        json = JSON.parse(text);
      } catch {
        // not json
      }

      if (!res.ok) {
        writeLog(` Upload failed (HTTP ${res.status})`);
        writeLog(text);
        return;
      }

      writeLog(" Upload success!");
      setResult(json);

      if (json?.pagesUrl) writeLog(` pagesUrl: ${json.pagesUrl}`);
      if (json?.downloadUrl) writeLog(` downloadUrl: ${json.downloadUrl}`);
      if (json?.path) writeLog(` path: ${json.path}`);
      if (json?.sha) writeLog(` sha: ${json.sha}`);

      // Build expected WEBP URL and poll for it
      if (json?.path) {
        const expectedWebp = buildWebpUrlFromUploadPath(REPO_PAGES_BASE, json.path);
        setWebpUrl(expectedWebp);
        writeLog(` Expected WEBP: ${expectedWebp}`);

        const ok = await waitForWebp(expectedWebp, writeLog);

        if (ok) {
          setWebpReady(true);

          //  set to webp output
          setTempSrc(expectedWebp);
          setUploadComplete(true);

          //  update UI immediately
          setDisplaySrc(expectedWebp);

          //  update metadata immediately
          onSrcChange?.(expectedWebp);

          writeLog(" Updated image src to WEBP output");
        } else {
          //  WEBP failed, use downloadUrl (raw GitHub URL)
          writeLog(" WEBP timeout, using raw GitHub URL");
          const fallbackUrl = json.downloadUrl || json.pagesUrl;
          setTempSrc(fallbackUrl);
          setUploadComplete(true);
          setDisplaySrc(fallbackUrl);
          onSrcChange?.(fallbackUrl);
          writeLog(` Updated image src to: ${fallbackUrl}`);
        }
      }
    } catch (err: any) {
      writeLog(` Upload error: ${err?.message || String(err)}`);
    } finally {
      setUploading(false);
    }
  };

  const handleFile = async (file?: File) => {
    if (!file || !file.type.startsWith("image/")) return;

    // 10MB limit
    if (file.size > 10 * 1024 * 1024) {
      writeLog(" File too large (max 10MB)");
      return;
    }

    await uploadToWorker(file);
  };

  const handleSave = () => {
    if (!tempSrc) return;

    setSaving(true);

    setTimeout(() => {
      //  update parent metadata
      onSrcChange?.(tempSrc);
      if (tempAlt) onAltChange?.(tempAlt);

      //  update local display immediately
      setDisplaySrc(tempSrc);
      setDisplayAlt(tempAlt || displayAlt);

      setSaving(false);
      setShowSuccess(true);

      setTimeout(() => {
        setShowSuccess(false);
        setModalOpen(false);
        resetModal();
      }, 800);
    }, 200);
  };

  const handleCancel = () => {
    setModalOpen(false);
    resetModal();
  };

  const resetModal = () => {
    setTempSrc("");
    setTempAlt("");
    setUploading(false);
    setUploadComplete(false);
    setSaving(false);
    setMode("upload");

    setLog("");
    setResult(null);
    setWebpUrl(null);
    setWebpReady(false);
  };

  const handleDrop = async (e: React.DragEvent) => {
    e.preventDefault();
    setDragOver(false);
    await handleFile(e.dataTransfer.files?.[0]);
  };

  return (
    <>
      <Stack spacing="xs">
        {displaySrc ? (
          <Image src={displaySrc} alt={displayAlt} radius="sm" mah={200} fit="contain" />
        ) : (
          <Text color="dimmed" size="sm">
            No image
          </Text>
        )}

        <TextInput label="Image URL" value={displaySrc || ""} readOnly size="xs" />

        <Button onClick={() => setModalOpen(true)} disabled={!!locked} variant="filled">
          Edit Image
        </Button>

        <TextInput
          label="Alt text"
          value={displayAlt || ""}
          onChange={(e) => {
            const nextAlt = e.currentTarget.value;
            setDisplayAlt(nextAlt);
            onAltChange?.(nextAlt);
          }}
          disabled={!!locked}
          size="xs"
        />
      </Stack>

      <Modal opened={modalOpen} onClose={handleCancel} title="Upload Image" size="lg" centered>
        <Stack spacing="md">
          <SegmentedControl
            value={mode}
            onChange={(v) => setMode(v as any)}
            data={[
              { label: "Upload Image", value: "upload" },
              { label: "Use Image URL", value: "url" },
            ]}
            fullWidth
          />

          {mode === "upload" ? (
            <>
              <input
                ref={inputRef}
                type="file"
                accept="image/jpeg,image/png,image/webp,image/jpg"
                style={{ display: "none" }}
                onChange={(e) => handleFile(e.target.files?.[0])}
              />

              <div
                onDragOver={(e) => {
                  e.preventDefault();
                  setDragOver(true);
                }}
                onDragLeave={() => setDragOver(false)}
                onDrop={handleDrop}
                onClick={() => !uploading && inputRef.current?.click()}
                style={{
                  border: `2px dashed ${dragOver ? "#228be6" : "#ced4da"}`,
                  borderRadius: 8,
                  padding: 48,
                  textAlign: "center",
                  cursor: uploading ? "default" : "pointer",
                  backgroundColor: dragOver ? "#f1f3f5" : "transparent",
                  transition: "all 0.2s",
                }}
              >
                {uploading ? (
                  <Text>Uploading...</Text>
                ) : (
                  <>
                    <Text size="lg" mb="xs">
                      Drop your image here or click to browse
                    </Text>
                    <Text size="xs" color="dimmed">
                      Uploads to Worker  GitHub  Actions  WEBP
                    </Text>
                    <Text size="xs" color="dimmed">
                      Max size: 10MB
                    </Text>
                  </>
                )}
              </div>

              {uploading && <Progress value={100} animated />}

              {webpUrl && (
                <div style={{ fontSize: 12, color: "#444" }}>
                  WEBP Output: {webpReady ? " Ready" : " Waiting"} <br />
                  <a href={webpUrl} target="_blank" rel="noreferrer">
                    {webpUrl}
                  </a>
                </div>
              )}

              <div style={{ marginTop: 10 }}>
                <div style={{ fontSize: 13, marginBottom: 6, color: "#333" }}>
                  Console
                </div>
                <pre
                  style={{
                    background: "#0b0b0d",
                    color: "#d9d9d9",
                    padding: 12,
                    borderRadius: 10,
                    fontSize: 12,
                    minHeight: 120,
                    overflow: "auto",
                    whiteSpace: "pre-wrap",
                    lineHeight: 1.35,
                  }}
                >
                  {log || "Drop an image to start..."}
                </pre>
              </div>
            </>
          ) : (
            <TextInput
              label="Image URL"
              placeholder="https://example.com/image.jpg"
              value={tempSrc}
              onChange={(e) => {
                const next = e.currentTarget.value;
                setTempSrc(next);
                setUploadComplete(!!next);
              }}
            />
          )}

          {tempSrc && (
            <>
              <Text size="sm" fw={500}>
                Preview:
              </Text>
              <Image src={tempSrc} alt="Preview" radius="sm" mah={300} fit="contain" />
            </>
          )}

          <TextInput
            label="Alt text (optional)"
            placeholder="Describe the image"
            value={tempAlt}
            onChange={(e) => setTempAlt(e.currentTarget.value)}
          />

          {saving && <Progress value={100} animated />}

          {showSuccess && (
            <Notification color="green" onClose={() => setShowSuccess(false)} withCloseButton={false}>
              Image saved successfully
            </Notification>
          )}

          <Group justify="flex-end" mt="md">
            <Button variant="subtle" onClick={handleCancel} disabled={saving}>
              Cancel
            </Button>

            <Button
              onClick={handleSave}
              disabled={!uploadComplete || !tempSrc || saving}
              loading={saving}
            >
              Save
            </Button>
          </Group>
        </Stack>
      </Modal>
    </>
  );
}