import { TextEditor } from "./PageEditorText";
import { LinkEditor } from "./PageEditorLink";
import { ImageEditor } from "./PageEditorImage";
import { VideoEditor } from "./PageEditorVideo";
import { UnknownEditor } from "./PageEditorIcon";
import React from "react";
import { Switch, Group, Text, Alert } from "@mantine/core";

export default function PageEditorDispatcher({ value }: any): JSX.Element | null {
  if (!value?.settings) return null;

  const { label, locked, superAdmin, onToggleLock, metadataLocked, userLocked } = value.settings;
  let type = value.settings?.type;

  // Defensive fallback: treat object-shaped nodes with a `src` key as images
  if (
    !type &&
    value?.defaultValue &&
    typeof value.defaultValue === "object" &&
    ("src" in value.defaultValue || "alt" in value.defaultValue)
  ) {
    type = "image";
  }

  // Determine visual state:
  // Superadmin mode: all inputs enabled (sub-toggle shows lock status)
  // Admin mode: disabled if locked
  const isEditable = superAdmin || !locked;
  const visuallyDisabled = !isEditable;
  const shouldShowSubToggle = superAdmin && onToggleLock;

  const Header = (
    <Group
      justify="space-between"
      mb={4}
      style={{
        opacity: visuallyDisabled ? 0.5 : 1,
        pointerEvents: visuallyDisabled ? 'none' : 'auto',
      }}
    >
      <Text size="sm" fw={500}>
        {label}
      </Text>

      {shouldShowSubToggle && (
        <Switch
          size="xs"
          checked={!userLocked}
          onChange={onToggleLock}
          title={metadataLocked ? "Locked by system" : "Toggle permission"}
        />
      )}
    </Group>
  );

  switch (type) {
    case "text":
      return (
        <>
          {Header}
          <TextEditor
            label={null}
            defaultValue={value.defaultValue ?? ""}
            locked={locked}
            multiline={
              typeof value.defaultValue === "string" &&
              value.defaultValue.length > 80
            }
            onChange={locked ? undefined : value.onChange}
            style={{
              opacity: visuallyDisabled ? 0.5 : 1,
              pointerEvents: visuallyDisabled ? 'none' : 'auto',
            }}
          />
        </>
      );

    case "link":
    case "jumplink":
      return (
        <>
          {Header}
          <LinkEditor
            label={null}
            defaultValue={value.defaultValue ?? ""}
            locked={locked}
            onChange={locked ? undefined : value.onChange}
            style={{
              opacity: visuallyDisabled ? 0.5 : 1,
              pointerEvents: visuallyDisabled ? 'none' : 'auto',
            }}
          />
        </>
      );

    case "image": {
      const srcVal =
        value?.src?.defaultValue ??
        (value?.defaultValue && typeof value.defaultValue === "object"
          ? value.defaultValue.src
          : typeof value.defaultValue === "string"
            ? value.defaultValue
            : undefined);
      const altVal =
        value?.alt?.defaultValue ??
        (value?.defaultValue && typeof value.defaultValue === "object"
          ? value.defaultValue.alt
          : undefined);

      const makeOnChange = (childName: string) => {
        if (value?.[childName]?.onChange) return value[childName].onChange;
        if (value?.onChange) {
          if (value?.defaultValue && typeof value.defaultValue === "object")
            return (v: any) => {
              const base = { ...value.defaultValue };
              base[childName] = v;
              value.onChange(base);
            };

          if (typeof value.defaultValue === "string") {
            if (childName === "src") return (v: any) => value.onChange(v);
            // converting string -> object when editing alt
            return (v: any) => value.onChange({ src: value.defaultValue, alt: v });
          }

          return (v: any) => {
            const base: any = {};
            base[childName] = v;
            value.onChange(base);
          };
        }
        return undefined;
      };

      return (
        <>
          {Header}
          <ImageEditor
            label={null}
            src={srcVal}
            alt={altVal}
            locked={locked}
            onSrcChange={locked ? undefined : makeOnChange("src")}
            onAltChange={locked ? undefined : makeOnChange("alt")}
            style={{
              opacity: visuallyDisabled ? 0.5 : 1,
              pointerEvents: visuallyDisabled ? 'none' : 'auto',
            }}
          />
        </>
      );
    }

    case "group": {
      const children = Object.entries(value)
        .filter(([k]) => k !== "settings")
        .map(([k, v]) => (
          <PageEditorDispatcher key={k} value={v} />
        ));

      return (
        <>
          {Header}
          <div style={{
            opacity: visuallyDisabled ? 0.5 : 1,
            pointerEvents: visuallyDisabled ? 'none' : 'auto',
            paddingLeft: '8px',
          }}>
            {children}
          </div>
        </>
      );
    }

    case "video":
      return (
        <>
          {Header}
          <VideoEditor
            label={null}
            videoUrl={value.defaultValue ?? ""}
            locked={locked}
            onChange={locked ? undefined : value.onChange}
            style={{
              opacity: visuallyDisabled ? 0.5 : 1,
              pointerEvents: visuallyDisabled ? 'none' : 'auto',
            }}
          />
        </>
      );

    case "array": {
      const items =
        value.array?.map((v: any, i: number) => (
          <div key={i} style={{ paddingLeft: 16 }}>
            <PageEditorDispatcher value={v} />
          </div>
        )) ?? [];

      if (items.length === 0 && Array.isArray(value.defaultValue)) {
        return (
          <>
            {Header}
            <Alert color="blue" variant="light">
              This collection is managed by a custom editor. Please use the specific section editor from the dropdown for the best experience.
            </Alert>
          </>
        )
      }

      return (
        <>
          {Header}
          <div style={{
            opacity: visuallyDisabled ? 0.5 : 1,
            pointerEvents: visuallyDisabled ? 'none' : 'auto',
          }}>
            {items}
          </div>
        </>
      );
    }

    default:
      return (
        <>
          {Header}
          <UnknownEditor label={label} />
        </>
      );
  }
}
