import { useEffect, useRef, useState } from "react";

// USAGE
/*

    const storage = useStorageObject();

    if (!storage) return null;

    const value = storage.getItem("whatever");
    storage.setItem("whatever", "hello");


*/

export function useStorage(
  type: "local" | "session" = "local"
): Storage | null {
  const ref = useRef<Storage | null>(null);

  useEffect(() => {
    if (typeof window === "undefined") return;

    try {
      ref.current =
        type === "local"
          ? window.localStorage
          : window.sessionStorage;
    } catch {
      ref.current = null;
    }
  }, [type]);

  return ref.current;
}


